import { Component, Input, OnInit, Output, EventEmitter } from "@angular/core";
import { ToastrService } from "ngx-toastr";
import { ManageSolutionsService } from "../../manage-solutions.service";
import { Select } from "@ngxs/store";
import { LoggedInUser } from "../../../state/model/loggedinuser.model";
import { Observable } from "rxjs";
import { LoggedInUserState, LoggedInUserStateModel } from "src/app/state/loggedinuser.state";
declare var $: any;
@Component({
	selector: "draft-item",
	templateUrl: "./draft-item.component.html",
	styleUrls: ["./draft-item.component.css"],
})
export class DraftItemComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;

	@Input() solutionData: any;
	@Input() Id: number;
	@Output() onDelete = new EventEmitter<any>();
	deleteLoading: boolean;

	constructor(private solutionService: ManageSolutionsService, private toastr: ToastrService) {
		this.loggedInUser$.subscribe((resp) => {
			this.loggedInUser = resp.LoggedInUser;
		});
	}

	ngOnInit() {}

	async deleteSolution() {
		try {
			this.deleteLoading = true;
			let deletedSolution = await this.solutionService.deleteSolution(this.solutionData.id);
			this.deleteLoading = false;
			$(`#deleteChallenge_${this.Id}`).modal("hide");
			this.toastr.info("Draft deleted");
			this.onDelete.emit(deletedSolution);
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}
}
