import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { SolutionService } from "./solution.service";
import { Validators, FormGroup, FormBuilder } from "@angular/forms";
import { ToastrService } from "ngx-toastr";
import { ActivatedRoute, Router } from "@angular/router";
import { IDropdownSettings } from "ng-multiselect-dropdown";
import * as _ from "lodash";
import { environment } from "../../../environments/environment";
import * as ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import { SelectSectorService } from "../../discover/select-sector/select-sector.service";
import { Location } from "@angular/common";
import { Select } from "@ngxs/store";
import { LoggedInUser } from "../../state/model/loggedinuser.model";
import { Observable } from "rxjs";
import { LoggedInUserState, LoggedInUserStateModel } from "src/app/state/loggedinuser.state";
declare var $: any;

@Component({
	selector: "app-solution-cataloging",
	templateUrl: "./solution-cataloging.component.html",
	styleUrls: ["./solution-cataloging.component.css"],
	encapsulation: ViewEncapsulation.None,
})
export class SolutionCatalogingComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;

	public Editor = ClassicEditor;
	initLoading: boolean;
	serverUrl = environment.server_url;
	loading: boolean = false;
	solutionStep1: boolean;
	solutionStep2: boolean;
	solutionStep3: boolean;

	addSolutionStep1: FormGroup;
	addSolutionStep2: FormGroup;
	addSolutionStep3: FormGroup;
	isSubmitted: any = {
		Step1: false,
		Step2: false,
		Step3: false,
	};

	challengeSector: any;
	challengeBasedOnSector: any;
	sectorDropdownList = [];
	challengeDropdownList = [];

	marketDomainList: any = [
		"Ecosystems",
		"Law Enforcement",
		"Healthcare",
		"Medical devices",
		"Telecom",
		"Finance",
		"Insurance",
		"Entertainment",
		"Gaming",
		"E-commerce",
		"Travel & Tourism",
		"Advertisement",
		"Information and Communication Technologies",
		"Future Cities",
		"Agriculture",
		"Transportation",
		"Others",
	];
	monthsArr: any = [0, 1, 2, 3, 4];
	weekArr: any = [0, 1, 2, 3, 4];
	ipStatusArr: any = ["Applied", "Published", "Granted", "Not Applicable"];
	dropdownSettings: IDropdownSettings;
	dropdownSettings2: IDropdownSettings;
	disableChallengeList: boolean = true;
	solutionsImplemented = [];
	cityAchievements = [];
	implemented_solutions: any;

	selectedChallengeSectors = [];
	selectedChallenges = [];
	tobeRemovedFilePaths = [];
	urlPattern: any = new RegExp("^(http|https)://");
	id: any;
	files: any[] = [];
	solutionGallery: any = [];
	goBackUrl: string = null;

	all_solutions: any[];
	draftLoading: boolean;
	cataloguedFor: any = null;
	sectorIdsFollowedByUser: number[];

	constructor(
		private _location: Location,
		private route: ActivatedRoute,
		private fb: FormBuilder,
		private _sol: SolutionService,
		private toastr: ToastrService,
		private router: Router,
		private _ss: SelectSectorService
	) {
		this.loggedInUser$.subscribe((resp) => {
			this.loggedInUser = resp.LoggedInUser;
		});

		this.route.queryParams.subscribe((params) => {
			// console.log(params.cataloguedFor);
			if (params.cataloguedFor) {
				this.cataloguedFor = JSON.parse(params.cataloguedFor);
				this.goBackUrl = `/challenge/${this.cataloguedFor.challengeId}`;
				// console.log(this.goBackUrl);
			}
		});

		this.Editor.defaultConfig = {
			toolbar: {
				items: ["heading", "|", "bold", "italic", "|", "bulletedList", "numberedList"],
			},
			image: {
				toolbar: ["imageStyle:full", "imageStyle:side", "|", "imageTextAlternative"],
			},
			table: {
				contentToolbar: ["tableColumn", "tableRow", "mergeTableCells"],
			},
			language: "en",
		};
	}

	async ngOnInit() {
		this.initLoading = true;
		this.solutionStep1 = true;
		this.solutionStep2 = false;
		this.solutionStep3 = false;
		await this.getChallengeSector();
		this.addSolutionStep1 = this.fb.group({
			solutionName: ["", [Validators.required]],
			challengeSectors: [[], [Validators.required]],
			challenges: [[], [Validators.required]],
			marketDomain: [[], [Validators.required]],
			solutionBrief: ["", [Validators.required, Validators.maxLength(700), Validators.minLength(100)]],
			valueProposition: ["", [Validators.required, Validators.maxLength(1000), Validators.minLength(100)]],
			tangibleBenefits: ["", [Validators.required, Validators.maxLength(750), Validators.minLength(100)]],
		});
		this.addSolutionStep2 = this.fb.group({
			solutionAdvantages: ["", [Validators.required, Validators.maxLength(750), Validators.minLength(100)]],
			solutionReadiness: ["", [Validators.required, Validators.maxLength(750), Validators.minLength(100)]],
			trackRecord: ["", [Validators.required, Validators.maxLength(750), Validators.minLength(100)]],
			implementationTimeMonth: ["", [Validators.required]],
			implementationTimeWeeks: ["", [Validators.required]],
		});
		this.addSolutionStep3 = this.fb.group({
			ipStatus: [""],
			identificationNumber: [""],
			expertsInvolved: ["", [Validators.required, Validators.maxLength(650), Validators.minLength(100)]],
			otherDetails: ["", [Validators.maxLength(500)]],
			video: ["", [Validators.pattern(this.urlPattern)]],
		});
		// pre selecting the challenge & sector
		if (this.cataloguedFor) {
			this.addSolutionStep1.patchValue({
				challengeSectors: [{ item_id: this.cataloguedFor.sectorId, item_text: this.cataloguedFor.sectorName }],
				challenges: [{ item_id: this.cataloguedFor.challengeId, item_text: this.cataloguedFor.challengeName }],
			});
			this.getChallengeBasedOnSector("");
		}
		this.initLoading = false;
	}

	get addSolutionControlStep1() {
		return this.addSolutionStep1.controls;
	}

	get addSolutionControlStep2() {
		return this.addSolutionStep2.controls;
	}

	get addSolutionControlStep3() {
		return this.addSolutionStep3.controls;
	}

	gotoStep(step) {
		switch (step) {
			case "step1":
				this.solutionStep1 = true;
				this.solutionStep2 = false;
				this.solutionStep3 = false;
				window.scroll(0, 0);
				break;
			case "step2":
				this.solutionStep1 = false;
				this.solutionStep2 = true;
				this.solutionStep3 = false;
				window.scroll(0, 0);
				break;
			case "step3":
				this.solutionStep1 = false;
				this.solutionStep2 = false;
				this.solutionStep3 = true;
				window.scroll(0, 0);
				break;
		}
	}

	backClicked() {
		if (this.goBackUrl) {
			this.router.navigate([this.goBackUrl]);
		} else {
			if (window.history.length > 1) {
				window.history.back();
			} else {
				this.router.navigate([`/provider/user/${this.loggedInUser.slug}`]);
			}
		}
	}

	solutionStep1Submit() {
		this.isSubmitted.Step1 = true;
		if (this.addSolutionStep1.valid) {
			this.gotoStep("step2");
		} else {
			this.toastr.error("please provide the required values to proceed further.", "Validation Error");
		}
	}

	solutionStep2Submit() {
		this.isSubmitted.Step2 = true;
		if (this.addSolutionStep2.valid) {
			this.gotoStep("step3");
		} else {
			this.toastr.error("please provide the required values to proceed further.", "Validation Error");
		}
	}

	async getChallengeSector() {
		try {
			this.challengeSector = await this._sol.getChallengeSectors();
			this.sectorIdsFollowedByUser = await this._sol.getSectorIdsFollowedByUser(this.loggedInUser.userTypeId);
			this.challengeSector.data.forEach((element) => {
				this.sectorDropdownList.push({
					item_id: parseInt(element.id),
					item_text: element.name,
				});
			});
			this.dropdownSettings = {
				singleSelection: false,
				idField: "item_id",
				textField: "item_text",
				enableCheckAll: false,
				allowSearchFilter: true,
			};
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	async getChallengeBasedOnSector(sector) {
		this.addSolutionStep1.controls.challengeSectors.markAsTouched();
		this.selectedChallenges = this.addSolutionStep1.value.challenges;
		this.challengeDropdownList = [];
		let idArr = this.addSolutionStep1.value.challengeSectors.map((el) => el.item_id);
		try {
			this.toastr.info("Fetching challenges based on sectors", "Info");
			this.challengeBasedOnSector = await this._sol.getChallenges(idArr);
			this.challengeBasedOnSector.data.forEach((element) => {
				this.challengeDropdownList = _.unionBy(this.challengeDropdownList, [{ item_id: parseInt(element.id), item_text: element.name }], "item_id");
			});
			this.selectedChallenges = _.intersectionBy(this.selectedChallenges, this.challengeDropdownList, "item_id");
			if (_.isArray(this.selectedChallenges)) {
				this.addSolutionStep1.patchValue({
					challenges: this.selectedChallenges,
				});
			} else {
				this.addSolutionStep1.patchValue({
					challenges: [this.selectedChallenges],
				});
			}
			this.disableChallengeList = false;
			this.addSolutionStep1.controls.challengeSectors.markAsTouched();
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	async saveSolution() {
		this.isSubmitted.Step3 = true;
		if (this.addSolutionStep1.valid && this.addSolutionStep2.valid && this.addSolutionStep3.valid) {
			this.loading = true;
			let solutionPostdata = {
				status: "active",
				name: this.addSolutionStep1.value.solutionName,
				stage: "Pilot",
				frg_provider_id: this.loggedInUser.userTypeId,
				market_domain: this.addSolutionStep1.value.marketDomain,
				solution_brief: this.addSolutionStep1.value.solutionBrief,
				video_link: this.addSolutionStep3.value.video,
				value_proposition: this.addSolutionStep1.value.valueProposition,
				tangible_benifits: this.addSolutionStep1.value.tangibleBenefits,
				solution_advantages: this.addSolutionStep2.value.solutionAdvantages,
				solution_readiness: this.addSolutionStep2.value.solutionReadiness,
				track_record: this.addSolutionStep2.value.trackRecord,
				other_details: this.addSolutionStep3.value.otherDetails,
				experts_involved: this.addSolutionStep3.value.expertsInvolved,
				implementation_time: {
					months: this.addSolutionStep2.value.implementationTimeMonth,
					weeks: this.addSolutionStep2.value.implementationTimeWeeks,
				},
				ip_details: {
					status: this.addSolutionStep3.value.ipStatus,
					id_number: this.addSolutionStep3.value.identificationNumber,
				},
			};
			try {
				let responseData: any = await this._sol.createNewSolution({ data: solutionPostdata });
				await Promise.all([this.uploadImages(responseData.data.id), this.associateChallengeSectors(responseData.data.id), this.associateChallenges(responseData.data.id)]).then(async(resp)=>{
					this.saveSectorArr();
					await this._sol.sendNotification(responseData.data.id);
				});
				this.loading = false;
				this.toastr.success("", "Solution added successfully");
				this.resetForm();
				this.router.navigate([`/provider/user/${this.loggedInUser.slug}/solution/${responseData.data.id}`]);
			} catch (err) {
				console.log(err);
				this.toastr.error("", "Something went wrong please try again");
			}
		} else {
			this.toastr.error("please provide the required values to proceed further.", "Validation Error");
		}
	}

	async uploadImages(id) {
		if (this.solutionGallery.length > 0) {
			let formData = new FormData();
			formData.append("slug", id);
			this.solutionGallery.forEach((file) => {
				formData.append("images", file);
			});
			await this._sol.uploadImages(formData);
		}
	}

	async associateChallenges(solutionId) {
		this.addSolutionStep1.value.challenges.forEach(async (element) => {
			await this._sol.addChallenges({
				data: {
					frg_challenge_id: element.item_id,
					frg_solution_id: solutionId,
				},
			});
		});
	}

	async associateChallengeSectors(solutionId) {
		this.addSolutionStep1.value.challengeSectors.forEach(async (element) => {
			await this._sol.addChallengeSectors({
				data: {
					frg_challenge_sector_id: element.item_id,
					frg_solution_id: solutionId,
				},
			});
		});
	}

	handleSaveGallery(files) {
		this.solutionGallery = files;
	}

	viewProviderProfile() {
		this.router.navigate(["/provider/user/" + this.loggedInUser.slug]);
	}
	viewSolutions() {
		this.router.navigate(["provider/user/" + this.loggedInUser.slug + "/all-solutions"]);
	}
	editSolution(sol_id: string) {
		// user/:slug/solution/:id/edit
		this.router.navigate(["/provider/user/" + this.loggedInUser.slug + "/solution/" + sol_id + "/edit"]);
	}

	prepareSelectedSectorArr() {
		var tmp_arr = [];
		return new Promise((resolve, reject) => {
			_.each(this.addSolutionStep1.value.challengeSectors, (itm, idx) => {
				var index = _.indexOf(this.sectorIdsFollowedByUser, itm.item_id);
				if (index == -1) {
					tmp_arr.push(itm.item_id);
				}
				if (this.addSolutionStep1.value.challengeSectors.length == idx + 1) {
					resolve(tmp_arr);
				}
			});
		});
	}

	saveSectorArr() {
		var sector_post_data = {};
		var temp_arr = [];
		return new Promise<void>((resolve, reject) => {
			this.prepareSelectedSectorArr().then((data: any) => {
				_.each(data, (itm, idx) => {
					if (this.loggedInUser.userType == "provider") {
						sector_post_data = {
							frg_challenge_sector_id: itm,
							frg_provider_id: this.loggedInUser.userTypeId,
						};
						temp_arr.push(sector_post_data);
					}
					if (data.length == temp_arr.length) {
						if (this.loggedInUser.userType == "provider") {
							this._ss.saveSelectedSectorAPI("provider", temp_arr).subscribe((resp: any) => {
								if (resp.status == true) {
									this.toastr.success("", "Sectors added successfully");
									resolve();
								}
							});
						}
					}
				});
			});
		});
	}

	async saveAsDraft() {
		if (this.addSolutionStep1.valid && this.addSolutionStep2.valid && this.addSolutionStep3.valid) {
			this.draftLoading = true;
			let solutionPostdata = {
				status: "draft",
				name: this.addSolutionStep1.value.solutionName,
				stage: "Pilot",
				frg_provider_id: this.loggedInUser.userTypeId,
				market_domain: this.addSolutionStep1.value.marketDomain,
				solution_brief: this.addSolutionStep1.value.solutionBrief,
				video_link: this.addSolutionStep3.value.video,
				value_proposition: this.addSolutionStep1.value.valueProposition,
				tangible_benifits: this.addSolutionStep1.value.tangibleBenefits,
				solution_advantages: this.addSolutionStep2.value.solutionAdvantages,
				solution_readiness: this.addSolutionStep2.value.solutionReadiness,
				track_record: this.addSolutionStep2.value.trackRecord,
				other_details: this.addSolutionStep3.value.otherDetails,
				experts_involved: this.addSolutionStep3.value.expertsInvolved,
				implementation_time: {
					months: this.addSolutionStep2.value.implementationTimeMonth,
					weeks: this.addSolutionStep2.value.implementationTimeWeeks,
				},
				ip_details: {
					status: this.addSolutionStep3.value.ipStatus,
					id_number: this.addSolutionStep3.value.identificationNumber,
				},
			};
			try {
				let responseData: any = await this._sol.createNewSolution({ data: solutionPostdata });
				await Promise.all([this.uploadImages(responseData.data.id), this.associateChallengeSectors(responseData.data.id), this.associateChallenges(responseData.data.id)]);
				this.draftLoading = false;
				this.resetForm();
				$("#confirmDialog").modal("hide");
				this.toastr.success("", "Solution saved as draft");
				this.router.navigate([`/provider/user/${this.loggedInUser.slug}/all-solutions`]);
			} catch (err) {
				console.log(err);
				this.toastr.error("", "Something went wrong please try again");
			}
		} else {
			this.isSubmitted.Step1 = false;
			this.isSubmitted.Step2 = true;
			this.isSubmitted.Step3 = true;
			this.toastr.warning("All mandatory fields has to be filled before saving as draft", "Mandatory fields required");
		}
	}

	ConfirmLeaveComponent() {
		$("#confirmDialog").modal("show");
	}

	async handleGoBack(action) {
		if (action === "save") {
			await this.saveAsDraft();
		} else {
			this.resetForm();
			$("#confirmDialog").modal("hide");
			this.backClicked();
		}
	}

	resetForm() {
		this.addSolutionStep1.reset();
		this.addSolutionStep2.reset();
		this.addSolutionStep3.reset();
		this.addSolutionStep1.markAsPristine();
		this.addSolutionStep2.markAsPristine();
		this.addSolutionStep3.markAsPristine();
	}
}
