	async getSolution() {
		try {
			this.loading = true;
			this.solution = await this.solutions.getSolutionbyId(this.id);
			this.profileData = await this.profileDataService.getProfile(this.userSlug);
			this.loading = false;
		} catch (err) {
			console.log(err);
		}
	}

	goBack() {
		if (window.history.length > 1) {
			// this.location.back();
			this.router.navigate([`/provider/user/${this.userSlug}`]);
		} else {
			this.router.navigate(["/discover/solutions"]);
		}
	}

	generateFake(count: number): Array<number> {
		const indexes = [];
		for (let i = 0; i < count; i++) {
			indexes.push(i);
		}
		return indexes;
	}

