import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ProfileInfoService {
  api_url = environment.api_url;
  constructor(private http: HttpClient) { }

  async getProfile(slug: string) {
    return await this.http.post(this.api_url + '/providers/select', { search: { slug: slug } }).toPromise();
  }

  async updateProfile(postdata: any) {
    return await this.http.post(this.api_url + '/providers/update', postdata).toPromise();
  }

  async updateAvatar(postdata: any) {
    return await this.http.post(this.api_url + '/providers/upload', postdata).toPromise();
  }

  async updateLogo(formData: any) {
    return await this.http.post(this.api_url + '/providers/company_logo', formData).toPromise();
  }

  async updateUser(postdata: any) {
    return await this.http.post(this.api_url + '/users/update', postdata).toPromise();
  }

  async updateLocation(postdata: any) {
    return await this.http.post(this.api_url + '/locations_and_providers/update', postdata).toPromise();
  }

  async changePassword(postdata: any) {
    return await this.http.post(this.api_url + '/users/change_password', postdata).toPromise();
  }

  async listLocations() {
    return await this.http.post(this.api_url + `/locations/list`, {
      sort: [{ name: "ASC" }]
    }).toPromise();
  }


}
