import { Injectable } from "@angular/core";
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, CanDeactivate } from "@angular/router";
import { Observable } from "rxjs";
import { SolutionCatalogingComponent } from "./solution-cataloging/solution-cataloging.component";
@Injectable({
	providedIn: "root",
})
export class SolutionDirtyCheckGuard implements CanDeactivate<SolutionCatalogingComponent> {
	canDeactivate(component: SolutionCatalogingComponent, next: ActivatedRouteSnapshot, router: RouterStateSnapshot) {
		if (component.addSolutionStep1.dirty || component.addSolutionStep2.dirty || component.addSolutionStep3.dirty) {
			component.ConfirmLeaveComponent();
		} else {
			return true;
		}
	}
}
