import { Injectable } from "@angular/core";
import { environment } from "../../environments/environment";
import { HttpClient } from "@angular/common/http";

@Injectable({
	providedIn: "root",
})
export class ManageSolutionsService {
	api_url = environment.api_url;
	constructor(private http: HttpClient) {}

	async getSolutionsByprovider(providerId: number) {
		return await this.http
			.post(this.api_url + "/solutions/select_mul", {
				filter: { status: "active", frg_provider_id: providerId },
				sort: [{ updated_at: "DESC" }],
			})
			.toPromise();
	}

	async deleteSolution(solutionId: number) {
		return await this.http
			.post(this.api_url + "/solutions/delete", {
				slug: { id: solutionId },
			})
			.toPromise();
	}
}
