import { Component, OnInit } from "@angular/core";
import { ProfileInfoService } from "../profile-info.service";
import { ManageSolutionsService } from "../manage-solutions.service";
import { ActivatedRoute } from "@angular/router";
import * as _ from "lodash";
import { ToastrService } from "ngx-toastr";

@Component({
	selector: "app-provider-user-profile",
	templateUrl: "./provider-user-profile.component.html",
	styleUrls: ["./provider-user-profile.component.css"],
})
export class ProviderUserProfileComponent implements OnInit {
	userSlug: string;
	profileLoading: boolean;
	solutionLoading: boolean;
	solutionData: any;
	profileData: any;
	constructor(private route: ActivatedRoute, private provider: ProfileInfoService, private solution: ManageSolutionsService, private toastr: ToastrService) {
		this.route.params.subscribe((params) => {
			this.userSlug = params.slug;
		});
	}

	async ngOnInit() {
		await this.fetchUserProfile();
	}

	async fetchUserProfile() {
		try {
			this.profileLoading = true;
			this.profileData = await this.provider.getProfile(this.userSlug);
			await this.fetchSolutionsByUser();
			this.profileLoading = false;
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	async fetchSolutionsByUser() {
		try {
			this.solutionLoading = true;
			if (this.profileData.data.solutions.length > 0) {
				this.solutionData = await this.solution.getSolutionsByprovider(this.profileData.data.id);
			}
			this.solutionLoading = false;
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	handleSolutionDelete(deletedSolution) {
		this.fetchSolutionsByUser();
	}
}
