import { Injectable } from "@angular/core";
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, CanDeactivate } from "@angular/router";
import { Observable } from "rxjs";
import { EditSolutionsComponent } from "./edit-solutions.component";
@Injectable({
	providedIn: "root",
})
export class EditsolutionDirtyCheckGuard implements CanDeactivate<EditSolutionsComponent> {
	canDeactivate(component: EditSolutionsComponent, currentRoute: ActivatedRouteSnapshot, nextState: RouterStateSnapshot) {
		if (component.solutionData.data.status == "draft" && (component.editSolutionStep1.dirty || component.editSolutionStep2.dirty || component.editSolutionStep3.dirty)) {
			component.ConfirmLeaveComponent();
		} else {
			return true;
		}
	}
}
