import { Component, OnInit } from "@angular/core";
import { AllSolutionsService } from "./all-solutions.service";
declare var $: any;
import { ToastrService } from "ngx-toastr";
import { ActivatedRoute, ParamMap, Router } from "@angular/router";
import { ProfileInfoService } from "../profile-info.service";
import { Select } from "@ngxs/store";
import { LoggedInUser } from "../../state/model/loggedinuser.model";
import { LoggedInUserState, LoggedInUserStateModel } from "../../state/loggedinuser.state";
import { Observable } from "rxjs";

@Component({
	selector: "app-all-solutions",
	templateUrl: "./all-solutions.component.html",
	styleUrls: ["./all-solutions.component.css"],
})
export class AllSolutionsComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;

	user_id: number;
	user_slug: string;
	profileData: any;
	profileLoading: boolean;
	loading: boolean;
	all_solutions: any[];
	del_sol_id: any;

	constructor(private _as: AllSolutionsService, private toastr: ToastrService, private router: Router, private route: ActivatedRoute, private profile: ProfileInfoService) {
		this.route.paramMap.subscribe((params: ParamMap) => {
			this.user_slug = params.get("slug");
		});
		this.loggedInUser$.subscribe((resp) => {
			this.user_id = resp.LoggedInUser.userTypeId;
		});
	}

	async ngOnInit() {
		this.profileLoading = true;
		this.profileData = await this.profile.getProfile(this.user_slug);
		this.getSolution(this.user_id);
		this.profileLoading = false;
	}

	async getSolution(user_id: number) {
		this.loading = true;
		this.all_solutions = await this._as.getSolutionsByprovider(user_id);
		// console.log(this.all_solutions);
		this.loading = false;
	}

	addSolutions() {
		this.router.navigate(["/provider/user/" + this.user_slug + "/solutions/create"]);
	}

	showDelModal(id) {
		$("#delete_sol").modal("show");
		this.del_sol_id = id;
	}

	deleteSolution() {
		this._as.deleteSolution(this.del_sol_id).subscribe((response: any) => {
			this.toastr.success("Solution has been deleted successfully!");
		});
		$("#delete_sol").modal("hide");
	}

	viewProviderProf() {
		this.router.navigate(["/provider/user/" + this.user_slug]);
	}

	editSolution(sol_id: string) {
		// user/:slug/solution/:id/edit
		this.router.navigate(["/provider/user/" + this.user_slug + "/solution/" + sol_id + "/edit"]);
	}

	handleDraftDelete(deletedDraft) {
		this.getSolution(this.user_id);
	}
}
