import { Component, OnInit } from '@angular/core';
import { ProfileInfoService } from "../profile-info.service"
import { ManageSolutionsService } from "../manage-solutions.service"
import { ActivatedRoute, Router } from '@angular/router';
import * as _ from 'lodash';
import { ToastrService } from 'ngx-toastr'

@Component({
  selector: 'app-provider-public-profile',
  templateUrl: './provider-public-profile.component.html',
  styleUrls: ['./provider-public-profile.component.css']
})
export class ProviderPublicProfileComponent implements OnInit {
  userSlug: string;
  profileLoading: boolean;
  solutionLoading:boolean;
  solutionData:any;
  profileData: any;
  constructor(
    private route: ActivatedRoute,
    private provider: ProfileInfoService,
    private solution: ManageSolutionsService,
    private toastr: ToastrService,
    private router: Router
  ) {
    this.route.params.subscribe(params => {
      this.userSlug = params.slug;
      // console.log(this.userSlug)
    });
  }

  async ngOnInit() {
    await this.fetchUserProfile()
  }

  async fetchUserProfile() {
    try {
      this.profileLoading = true;
      this.profileData = await this.provider.getProfile(this.userSlug);
      await this.fetchSolutionsByUser();
      this.profileLoading = false;
    } catch (err) {
      console.log(err);
      this.toastr.error('', 'Something went wrong please try again');
    }
  }

  async fetchSolutionsByUser() {
    try {
      this.solutionLoading = true;
      if(this.profileData.data.solutions.length > 0){
        this.solutionData = await this.solution.getSolutionsByprovider(this.profileData.data.id);
      }     
      this.solutionLoading = false;
    } catch (err) {
      console.log(err);
      this.toastr.error('', 'Something went wrong please try again');
    }
  }
}
