import { Injectable } from "@angular/core";
import { environment } from "../../../environments/environment";
import { HttpClient } from "@angular/common/http";

@Injectable({
	providedIn: "root",
})
export class AllSolutionsService {
	api_url = environment.api_url;
	constructor(private http: HttpClient) {}

	async getSolutionsByprovider(providerId: number) {
		// console.log("getting drafts by the provider");
		let resp: any = await this.http.post(this.api_url + "/solutions/select_mul", { filter: { status: "draft", frg_provider_id: providerId } }).toPromise();
		return resp.data;
	}

	deleteSolution(solutionId: number) {
		return this.http.post(this.api_url + "/solutions/delete", {
			slug: { id: solutionId },
		});
	}
}
