import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { IDropdownSettings } from "ng-multiselect-dropdown";
import { Validators, FormGroup, FormBuilder } from "@angular/forms";

import { ToastrService } from "ngx-toastr";
import * as _ from "lodash";
import { environment } from "../../../environments/environment";
import { EditSolutionsServiceService } from "./edit-solutions-service.service";
import * as ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import { AllSolutionsService } from "../all-solutions/all-solutions.service";
import { SolutionService } from "../solution-cataloging/solution.service";
import { SelectSectorService } from "src/app/discover/select-sector/select-sector.service";
import { Select } from "@ngxs/store";
import { LoggedInUser } from "../../state/model/loggedinuser.model";
import { Observable } from "rxjs";
import { LoggedInUserState, LoggedInUserStateModel } from "src/app/state/loggedinuser.state";

declare var $: any;

@Component({
	selector: "app-edit-solutions",
	templateUrl: "./edit-solutions.component.html",
	styleUrls: ["./edit-solutions.component.css"],
})
export class EditSolutionsComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;

	public Editor = ClassicEditor;

	initLoading: boolean;
	solutionData: any;
	serverUrl = environment.server_url;
	loading: boolean = false;
	solutionStep1: boolean;
	solutionStep2: boolean;
	solutionStep3: boolean;
	editSolutionStep1: FormGroup;
	editSolutionStep2: FormGroup;
	editSolutionStep3: FormGroup;
	isSubmitted: any = {
		Step1: false,
		Step2: false,
		Step3: false,
	};
	challengeSector: any;
	challengeBasedOnSector: any;
	sectorDropdownList = [];
	dropdownSettings: IDropdownSettings;
	selectedSectors = [];
	challengeDropdownList = [];
	disableChallengeList: boolean = true;
	selectedChallengeSectors = [];
	selectedChallenges = [];
	tobeRemovedFilePaths = [];
	urlPattern: any = new RegExp("^(http|https)://");
	id: number;
	solutionProductImages: File[] = [];
	currentProductImages: any = [];
	domainDropDownList: string[] = [
		"Ecosystems",
		"Law Enforcement",
		"Healthcare",
		"Medical devices",
		"Telecom",
		"Finance",
		"Insurance",
		"Entertainment",
		"Gaming",
		"E-commerce",
		"Travel & Tourism",
		"Advertisement",
		"Information and Communication Technologies",
		"Future Cities",
		"Agriculture",
		"Transportation",
		"Others",
	];
	domainDropDownSettings: IDropdownSettings = {
		singleSelection: false,
		textField: "item_text",
		enableCheckAll: false,
		allowSearchFilter: true,
	};
	monthsArr: any = [0, 1, 2, 3, 4];
	weekArr: any = [0, 1, 2, 3, 4];
	ipStatusArr: any = ["Applied", "Published", "Granted", "Not Applicable"];
	all_solutions: any[];
	draftLoading: boolean;
	sectorIdsFollowedByUser: number[];

	constructor(
		private route: ActivatedRoute,
		private fb: FormBuilder,
		private _sol: EditSolutionsServiceService,
		private toastr: ToastrService,
		private router: Router,
		private _as: AllSolutionsService,
		private solutionService: SolutionService,
		private _ss: SelectSectorService
	) {
		this.loggedInUser$.subscribe((resp) => {
			this.loggedInUser = resp.LoggedInUser;
		});
		this.Editor.defaultConfig = {
			toolbar: {
				items: ["heading", "|", "bold", "italic", "bulletedList", "numberedList", "link"],
			},
			image: {
				toolbar: ["imageStyle:full", "imageStyle:side", "|", "imageTextAlternative"],
			},
			table: {
				contentToolbar: ["tableColumn", "tableRow", "mergeTableCells"],
			},
			language: "en",
		};
	}

	async ngOnInit() {
		this.route.params.subscribe((params) => {
			this.id = params.id;
		});

		this.initLoading = true;
		this.solutionStep1 = true;
		this.solutionStep2 = false;
		this.solutionStep3 = false;
		await this.getChallengeSector();
		this.editSolutionStep1 = this.fb.group({
			solutionName: ["", [Validators.required]],
			challengeSectors: [[], [Validators.required]],
			challenges: [[], [Validators.required]],
			marketDomain: [[], [Validators.required]],
			solutionBrief: ["", [Validators.required, Validators.maxLength(700), Validators.minLength(100)]],
			valueProposition: ["", [Validators.required, Validators.maxLength(1000), Validators.minLength(100)]],
			tangibleBenefits: ["", [Validators.required, Validators.maxLength(750), Validators.minLength(100)]],
		});
		this.editSolutionStep2 = this.fb.group({
			solutionAdvantages: ["", [Validators.required, Validators.maxLength(750), Validators.minLength(100)]],
			solutionReadiness: ["", [Validators.required, Validators.maxLength(750), Validators.minLength(100)]],
			trackRecord: ["", [Validators.required, Validators.maxLength(750), Validators.minLength(100)]],
			implementationTimeMonth: ["", [Validators.required]],
			implementationTimeWeeks: ["", [Validators.required]],
		});
		this.editSolutionStep3 = this.fb.group({
			ipStatus: [""],
			identificationNumber: [""],
			expertsInvolved: ["", [Validators.required, Validators.maxLength(650), Validators.minLength(100)]],
			otherDetails: ["", [Validators.maxLength(500)]],
			video: ["", [Validators.pattern(this.urlPattern)]],
		});
		await this.fetchSolutionAndMapValuesToFormControls();
		this.initLoading = false;
	}

	get editSolutionControlStep1() {
		return this.editSolutionStep1.controls;
	}

	get editSolutionControlStep2() {
		return this.editSolutionStep2.controls;
	}

	get editSolutionControlStep3() {
		return this.editSolutionStep3.controls;
	}

	async getChallengeSector() {
		try {
			this.challengeSector = await this._sol.getChallengeSectors();
			this.challengeSector.data.forEach((element) => {
				this.sectorDropdownList.push({
					item_id: parseInt(element.id),
					item_text: element.name,
				});
			});
			this.dropdownSettings = {
				singleSelection: false,
				idField: "item_id",
				textField: "item_text",
				enableCheckAll: false,
				allowSearchFilter: true,
			};
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	async fetchSolutionAndMapValuesToFormControls() {
		// fetch solution by id
		this.solutionData = await this._sol.getSolution(this.id);
		this.sectorIdsFollowedByUser = await this._sol.getSectorIdsFollowedByUser(this.loggedInUser.userTypeId);
		// currently selected sectors
		this.selectedSectors = this.solutionData.data.challenge_sectors.map((el) => {
			return { item_id: el.id, item_text: el.name };
		});
		// currently selected challenges
		this.selectedChallenges = this.solutionData.data.challenges.map((el) => {
			return { item_id: el.id, item_text: el.name };
		});
		// patch values with form fields
		this.editSolutionStep1.patchValue({
			solutionName: this.solutionData.data.name,
			challengeSectors: this.selectedSectors,
			challenges: this.selectedChallenges,
			marketDomain: this.solutionData.data.market_domain,
			solutionBrief: this.solutionData.data.solution_brief,
			valueProposition: this.solutionData.data.value_proposition,
			tangibleBenefits: this.solutionData.data.tangible_benifits,
		});
		this.editSolutionStep2.patchValue({
			solutionAdvantages: this.solutionData.data.solution_advantages,
			solutionReadiness: this.solutionData.data.solution_readiness,
			trackRecord: this.solutionData.data.track_record,
			implementationTimeMonth: this.solutionData.data.implementation_time.months,
			implementationTimeWeeks: this.solutionData.data.implementation_time.weeks,
		});
		this.editSolutionStep3.patchValue({
			ipStatus: this.solutionData.data.ip_details.status,
			identificationNumber: this.solutionData.data.ip_details.id_number,
			expertsInvolved: this.solutionData.data.experts_involved,
			otherDetails: this.solutionData.data.other_details,
			video: this.solutionData.data.video_link,
		});
		this.currentProductImages = _.cloneDeep(this.solutionData.data.images);
		await this.initChallengesDropdown();
	}

	async initChallengesDropdown() {
		this.disableChallengeList = true;
		let idArr = [];
		this.challengeDropdownList = [];
		this.editSolutionStep1.value.challengeSectors.forEach((element) => {
			idArr.push(element.item_id);
		});
		try {
			this.challengeBasedOnSector = await this._sol.getChallenges(idArr);
			this.challengeDropdownList = this.challengeBasedOnSector.data.map((el) => {
				return { item_id: el.id, item_text: el.name };
			});
			this.disableChallengeList = false;
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	async getChallengeBasedOnSector() {
		this.disableChallengeList = true;
		// manually setting the touched event since plugin doesnt work properly
		this.editSolutionStep1.controls.challengeSectors.markAsTouched();
		this.selectedChallenges = this.editSolutionStep1.value.challenges;
		this.challengeDropdownList = [];
		let idArr = this.editSolutionStep1.value.challengeSectors.map((el) => el.item_id);
		try {
			this.toastr.info("Fetching challenges based on sectors", "Info");
			this.challengeBasedOnSector = await this._sol.getChallenges(idArr);
			this.challengeBasedOnSector.data.forEach((element) => {
				this.challengeDropdownList = _.unionBy(this.challengeDropdownList, [{ item_id: parseInt(element.id), item_text: element.name }], "item_id");
			});
			this.selectedChallenges = _.intersectionBy(this.selectedChallenges, this.challengeDropdownList, "item_id");
			if (_.isArray(this.selectedChallenges)) {
				this.editSolutionStep1.patchValue({
					challenges: this.selectedChallenges,
				});
			} else {
				this.editSolutionStep1.patchValue({
					challenges: [this.selectedChallenges],
				});
			}
			// manually setting the touched event since plugin doesnt work properly
			this.editSolutionStep1.controls.challenges.markAsTouched();
			// console.log("selected values should be",this.editSolutionStep1.value.challenges);
			this.disableChallengeList = false;
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	handleChallengeChange(event) {
		// manually setting the touched event since plugin doesnt work properly
		this.editSolutionStep1.controls.challenges.markAsTouched();
	}

	gotoStep(step) {
		switch (step) {
			case "step1":
				this.solutionStep1 = true;
				this.solutionStep2 = false;
				this.solutionStep3 = false;
				window.scroll(0, 0);
				break;
			case "step2":
				this.solutionStep1 = false;
				this.solutionStep2 = true;
				this.solutionStep3 = false;
				window.scroll(0, 0);
				break;
			case "step3":
				this.solutionStep1 = false;
				this.solutionStep2 = false;
				this.solutionStep3 = true;
				window.scroll(0, 0);
				break;
		}
	}

	solutionStep1Submit() {
		this.isSubmitted.Step1 = true;
		if (this.editSolutionStep1.valid) {
			this.gotoStep("step2");
		} else {
			this.toastr.error("please provide the required values to proceed further.", "Validation Error");
		}
	}

	solutionStep2Submit() {
		this.isSubmitted.Step2 = true;
		if (this.editSolutionStep2.valid) {
			this.gotoStep("step3");
		} else {
			this.toastr.error("please provide the required values to proceed further.", "Validation Error");
		}
	}

	async saveSolution() {
		this.isSubmitted.Step3 = true;
		if (this.editSolutionStep1.valid && this.editSolutionStep2.valid && this.editSolutionStep3.valid) {
			this.loading = true;
			let solutionPostData = {
				slug: { id: this.id },
				data: {
					name: this.editSolutionStep1.value.solutionName,
					stage: "Pilot",
					market_domain: this.editSolutionStep1.value.marketDomain,
					solution_brief: this.editSolutionStep1.value.solutionBrief,
					video_link: this.editSolutionStep3.value.video,
					value_proposition: this.editSolutionStep1.value.valueProposition,
					tangible_benifits: this.editSolutionStep1.value.tangibleBenefits,
					solution_advantages: this.editSolutionStep2.value.solutionAdvantages,
					solution_readiness: this.editSolutionStep2.value.solutionReadiness,
					track_record: this.editSolutionStep2.value.trackRecord,
					other_details: this.editSolutionStep3.value.otherDetails,
					experts_involved: this.editSolutionStep3.value.expertsInvolved,
					implementation_time: {
						months: this.editSolutionStep2.value.implementationTimeMonth,
						weeks: this.editSolutionStep2.value.implementationTimeWeeks,
					},
					ip_details: {
						status: this.editSolutionStep3.value.ipStatus,
						id_number: this.editSolutionStep3.value.identificationNumber,
					},
				},
			};
			try {
				let updatedCity: any = await this._sol.updateSolution(solutionPostData);
				await Promise.all([
					this.associateChallengeSectors(updatedCity.data.id),
					this.associateChallenges(updatedCity.data.id),
					await this.filesRemovedFromProductImages(updatedCity.data.id),
					await this.saveNewProductImages(updatedCity.data.id),
				]);
				this.saveSectorArr();
				this.loading = false;
				this.toastr.success("", "Solution updated successfully!");
				this.router.navigate([`/provider/user/${this.loggedInUser.slug}/solution/${updatedCity.data.id}`]);
			} catch (err) {
				console.log(err);
				this.toastr.error("", "Something went wrong please try again");
			}
		} else {
			this.toastr.error("please provide the required values to proceed further.", "Validation Error");
		}
	}

	async publishSolution() {
		this.isSubmitted.Step3 = true;
		if (this.editSolutionStep1.valid && this.editSolutionStep2.valid && this.editSolutionStep3.valid) {
			this.loading = true;
			let solutionPostData = {
				slug: { id: this.id },
				data: {
					status: "active",
					name: this.editSolutionStep1.value.solutionName,
					stage: "Pilot",
					market_domain: this.editSolutionStep1.value.marketDomain,
					solution_brief: this.editSolutionStep1.value.solutionBrief,
					video_link: this.editSolutionStep3.value.video,
					value_proposition: this.editSolutionStep1.value.valueProposition,
					tangible_benifits: this.editSolutionStep1.value.tangibleBenefits,
					solution_advantages: this.editSolutionStep2.value.solutionAdvantages,
					solution_readiness: this.editSolutionStep2.value.solutionReadiness,
					track_record: this.editSolutionStep2.value.trackRecord,
					other_details: this.editSolutionStep3.value.otherDetails,
					experts_involved: this.editSolutionStep3.value.expertsInvolved,
					implementation_time: {
						months: this.editSolutionStep2.value.implementationTimeMonth,
						weeks: this.editSolutionStep2.value.implementationTimeWeeks,
					},
					ip_details: {
						status: this.editSolutionStep3.value.ipStatus,
						id_number: this.editSolutionStep3.value.identificationNumber,
					},
				},
			};
			try {
				let updatedCity: any = await this._sol.updateSolution(solutionPostData);
				await Promise.all([
					this.associateChallengeSectors(updatedCity.data.id),
					this.associateChallenges(updatedCity.data.id),
					await this.filesRemovedFromProductImages(updatedCity.data.id),
					await this.saveNewProductImages(updatedCity.data.id),
				]);
				this.saveSectorArr();
				await this.solutionService.sendNotification(updatedCity.data.id);
				this.loading = false;
				this.resetForm();
				this.toastr.success("", "Solution updated successfully!");
				this.router.navigate([`/provider/user/${this.loggedInUser.slug}/solution/${updatedCity.data.id}`]);
			} catch (err) {
				console.log(err);
				this.toastr.error("", "Something went wrong please try again");
			}
		} else {
			this.toastr.error("please provide the required values to proceed further.", "Validation Error");
		}
	}

	prepareSelectedSectorArr() {
		var tmp_arr = [];
		return new Promise((resolve, reject) => {
			_.each(this.editSolutionStep1.value.challengeSectors, (itm, idx) => {
				var index = _.indexOf(this.sectorIdsFollowedByUser, itm.item_id);
				if (index == -1) {
					tmp_arr.push(itm.item_id);
				}
				if (this.editSolutionStep1.value.challengeSectors.length == idx + 1) {
					resolve(tmp_arr);
				}
			});
		});
	}

	saveSectorArr() {
		var sector_post_data = {};
		var temp_arr = [];
		return new Promise<void>((resolve, reject) => {
			this.prepareSelectedSectorArr().then((data: any) => {
				_.each(data, (itm, idx) => {
					if (this.loggedInUser.userType == "provider") {
						sector_post_data = {
							frg_challenge_sector_id: itm,
							frg_provider_id: this.loggedInUser.userTypeId,
						};
						temp_arr.push(sector_post_data);
					}
					if (data.length == temp_arr.length) {
						if (this.loggedInUser.userType == "provider") {
							this._ss.saveSelectedSectorAPI("provider", temp_arr).subscribe((resp: any) => {
								if (resp.status == true) {
									this.toastr.success("", "Sectors added successfully");
									resolve();
								}
							});
						}
					}
				});
			});
		});
	}

	async saveAsDraft() {
		this.isSubmitted.Step3 = true;
		if (this.editSolutionStep1.valid && this.editSolutionStep2.valid && this.editSolutionStep3.valid) {
			this.draftLoading = true;
			let solutionPostData = {
				slug: { id: this.id },
				data: {
					status: "draft",
					name: this.editSolutionStep1.value.solutionName,
					stage: "Pilot",
					market_domain: this.editSolutionStep1.value.marketDomain,
					solution_brief: this.editSolutionStep1.value.solutionBrief,
					video_link: this.editSolutionStep3.value.video,
					value_proposition: this.editSolutionStep1.value.valueProposition,
					tangible_benifits: this.editSolutionStep1.value.tangibleBenefits,
					solution_advantages: this.editSolutionStep2.value.solutionAdvantages,
					solution_readiness: this.editSolutionStep2.value.solutionReadiness,
					track_record: this.editSolutionStep2.value.trackRecord,
					other_details: this.editSolutionStep3.value.otherDetails,
					experts_involved: this.editSolutionStep3.value.expertsInvolved,
					implementation_time: {
						months: this.editSolutionStep2.value.implementationTimeMonth,
						weeks: this.editSolutionStep2.value.implementationTimeWeeks,
					},
					ip_details: {
						status: this.editSolutionStep3.value.ipStatus,
						id_number: this.editSolutionStep3.value.identificationNumber,
					},
				},
			};
			try {
				let updatedCity: any = await this._sol.updateSolution(solutionPostData);
				await Promise.all([
					this.associateChallengeSectors(updatedCity.data.id),
					this.associateChallenges(updatedCity.data.id),
					await this.filesRemovedFromProductImages(updatedCity.data.id),
					await this.saveNewProductImages(updatedCity.data.id),
				]);
				this.draftLoading = false;
				this.toastr.info("", "Solution draft saved");
				$("#confirmDialog").modal("hide");
				this.resetForm();
				this.router.navigate([`/provider/user/${this.loggedInUser.slug}/all-solutions`]);
			} catch (err) {
				console.log(err);
				this.toastr.error("", "Something went wrong please try again");
			}
		} else {
			this.toastr.warning("All mandatory fields has to be filled before saving as draft", "Mandatory fields required");
		}
	}

	async associateChallenges(solutionId: number) {
		if (this.editSolutionStep1.controls.challenges.touched) {
			// removing all challenges which are previously set.
			let filterPreviousAssociationsBySolutionId: any = await this._sol.getSolutionsChallengesAssoc(solutionId);
			let idsToBeDeleted = filterPreviousAssociationsBySolutionId.data.map((e) => e.id);
			await this._sol.deleteSolutionChallengesAssoc(idsToBeDeleted);
			// newly creating the association
			if (this.editSolutionStep1.value.challenges.length > 0) {
				this.editSolutionStep1.value.challenges.forEach(async (el) => {
					await this._sol.addChallenges({
						data: {
							frg_solution_id: solutionId,
							frg_challenge_id: el.item_id,
						},
					});
				});
			}
		}
	}

	async associateChallengeSectors(solutionId: number) {
		if (this.editSolutionStep1.controls.challengeSectors.touched) {
			// removing all sectors which are previously set.
			let filterPreviousAssociationsBySolutionId: any = await this._sol.getSolutionsChallengeSectorsAssoc(solutionId);
			let idsToBeDeleted = filterPreviousAssociationsBySolutionId.data.map((e) => e.id);
			await this._sol.deleteSolutionChallengeSectorsAssoc(idsToBeDeleted);
			// newly creating the association
			if (this.editSolutionStep1.value.challengeSectors.length > 0) {
				this.editSolutionStep1.value.challengeSectors.forEach(async (el) => {
					await this._sol.addChallengeSectors({
						data: {
							frg_challenge_sector_id: el.item_id,
							frg_solution_id: solutionId,
						},
					});
				});
			}
		}
	}

	async filesRemovedFromProductImages(id: number) {
		if (this.tobeRemovedFilePaths.length > 0) {
			await this._sol.removeFromProductImages({
				slug: { id: id },
				data: { remove_paths: this.tobeRemovedFilePaths },
			});
		}
	}

	async saveNewProductImages(id) {
		if (!this.solutionData.data.images) {
			if (this.solutionProductImages.length > 0) {
				let formData = new FormData();
				formData.append("slug", id);
				this.solutionProductImages.forEach((file) => {
					formData.append("images", file);
				});
				await this._sol.uploadSolutionImages(formData);
			}
		} else {
			if (this.solutionProductImages.length > 0) {
				let formData = new FormData();
				formData.append("slug", id);
				this.solutionProductImages.forEach((file) => {
					formData.append("images", file);
				});
				await this._sol.addtoProductImages(formData);
			}
		}
	}

	handleSaveGallery(files) {
		// console.log(files);
		let ProductImagesBeforeChange = this.solutionData.data.images;
		// splitting the array with
		let newFiles: File[] = files.filter((e) => {
			if (typeof e == "object") {
				return e;
			}
		});
		let updatedFilePaths: string = files.filter((e) => {
			if (typeof e == "string") {
				return e;
			}
		});
		if (ProductImagesBeforeChange) this.tobeRemovedFilePaths = ProductImagesBeforeChange.filter((x) => !updatedFilePaths.includes(x));
		this.solutionProductImages = newFiles;
		// console.log('newly added files',this.solutionProductImages);
		// console.log('files to be removed', this.tobeRemovedFilePaths);
	}

	viewProviderProfile() {
		this.router.navigate(["/provider/user/" + this.loggedInUser.slug]);
	}
	addSolution() {
		this.router.navigate(["/provider/user/" + this.loggedInUser.slug + "/solutions/create"]);
	}
	viewSolution() {
		this.router.navigate(["provider/user/" + this.loggedInUser.slug + "/all-solutions"]);
	}

	editSolution(sol_id: string) {
		// user/:slug/solution/:id/edit
		this.router.navigate(["/provider/user/" + this.loggedInUser.slug + "/solution/" + sol_id + "/edit"]);
	}

	ConfirmLeaveComponent() {
		$("#confirmDialog").modal("show");
	}

	async handleGoBack(action) {
		if (action === "save") {
			await this.saveAsDraft();
		} else {
			this.resetForm();
			$("#confirmDialog").modal("hide");
			window.history.back();
		}
	}

	resetForm() {
		this.editSolutionStep1.reset();
		this.editSolutionStep2.reset();
		this.editSolutionStep3.reset();
		this.editSolutionStep1.markAsPristine();
		this.editSolutionStep2.markAsPristine();
		this.editSolutionStep3.markAsPristine();
	}
}
