import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { HttpClient } from '@angular/common/http';

@Injectable({
	providedIn: 'root'
})

export class unsolicitedProposalService {
	api_url = environment.api_url;
	constructor(private http: HttpClient) { }

	async getSmartCities() {
		return await this.http
			.post(this.api_url + "/smart_cities/select_mul", {
				pg: {
					offset: 0,
					limit: 200,
				},
				filter: {
					status: 'active'
				}
			})
			.toPromise();
	}

	async createChallenge(postData) {
		return await this.http.post(this.api_url + `/challenges/create`, postData).toPromise();
	}

	async createChallengeSmartcityAsscoiation(postData) {
		return await this.http.post(this.api_url + `/unsolicited_proposal/create_chall_smart_uns_prop `, postData).toPromise();
	}

	async associateChallengewithCity(postData:any){
		var post_data = {
			data:{
				frg_challenge_id: postData.frg_challenge_id,
				frg_smart_city_id: postData.frg_smart_city_id,
				uns_proposal:true
			}
		}
		// console.log(post_data)
		return await this.http.post(this.api_url + "/challenges_and_smart_cities/create", post_data).toPromise();
	}

	async createUnsolProposal(post_data:any){
		return await this.http.post(this.api_url + "/unsolicited_proposal/create", post_data).toPromise();
	}

	async uploadImages(post_data){
		return await this.http.post(this.api_url + "/unsolicited_proposal/uns_file_upload", post_data).toPromise();
	}
}