import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { environment } from '../../../../environments/environment';
import * as _ from 'lodash';
import { ToastrService } from 'ngx-toastr';
import { ManageSolutionsService } from '../../manage-solutions.service';

@Component({
  selector: 'solution-item',
  templateUrl: './solution-item.component.html',
  styleUrls: ['./solution-item.component.css']
})

export class SolutionItemComponent implements OnInit {
  @Input() Id: number;
  @Input() solutionData: any;
  @Input() viewType: string = 'public'
  @Output() onDelete = new EventEmitter<any>();
  serverUrl: string = environment.server_url;
  deleteLoading: boolean;
  constructor(private solution: ManageSolutionsService, private toastr: ToastrService) { }

  ngOnInit() { }

  async deleteSolution(id: number) {
    try {
      this.deleteLoading = true;
      let resp: any = await this.solution.deleteSolution(id)
      this.deleteLoading = false;
      document.getElementById(`deleteSolution${this.Id}`).click();
      this.toastr.success('', 'Solution deleted successfully');
      this.onDelete.emit(resp);
    } catch (err) {
      console.log(err)
      this.toastr.error('', 'Something went wrong please try again');
    }
  }

}
