import { Injectable } from "@angular/core";
import { environment } from "../../../environments/environment";
import { HttpClient } from "@angular/common/http";
import * as _ from "lodash";

@Injectable({
	providedIn: "root",
})
export class SelectSectorService {
	api_url = environment.api_url;

	constructor(private http: HttpClient) {}

	saveSelectedSectorAPI(type: string, post_data: any) {
		if (type == "seeker") {
			return this.http.post(this.api_url + "/challenge_sectors_seekers/create_mul", post_data);
		} else if (type == "provider") {
			return this.http.post(this.api_url + "/challenge_sectors_and_providers/create_mul", post_data);
		}
	}

	getSelectedSectorAPI(type: string, data) {
		if (type == "seeker") {
			return this.http.post(this.api_url + "/challenge_sectors_seekers/select_mul", data);
		} else if (type == "provider") {
			return this.http.post(this.api_url + "/challenge_sectors_and_providers/select_mul", data);
		}
	}
	removeSectorsAPI(type: string, data: any) {
		if (type == "seeker") {
			return this.http.post(this.api_url + "/challenge_sectors_seekers/delete_mul", data);
		} else if (type == "provider") {
			return this.http.post(this.api_url + "/challenge_sectors_and_providers/delete_mul", data);
		}
	}

	async getSectorIdsFollowedBySeekerUnderChallenges(seekerId: number) {
		let resp: any = await this.http
			.post(this.api_url + "/anchors/select_mul", {
				filter: {
					frg_seeker_id: seekerId,
					status: "active",
				},
			})
			.toPromise();
		let sectorId: number[] = resp.data.map((e) => e.frg_challenge_id.frg_challenge_sector_id);
		return sectorId;
	}

	async getSectorIdsFollowedByProviderUnderSolutions(providerId: number) {
		let resp: any = await this.http
			.post(this.api_url + "/solutions/select_mul", {
				filter: {
					frg_provider_id: providerId,
					status: "active",
				},
			})
			.toPromise();
		// console.log(resp);
		let sectorId: number[] = _.flattenDeep(resp.data.map((e) => e.challenge_sectors.map((sector) => sector.id)));
		// console.log("before uniq", sectorId);
		return _.uniq(sectorId);
	}

	async fetchSector() {
		return this.http
			.post(this.api_url + "/challenge_sectors/select_mul", {
				filter: { status: "active" },
				sort: "created_at DESC",
				pg: {
					limit: 200,
					skip: 0,
				},
			})
			.toPromise();
	}
}
