import { Injectable } from "@angular/core";
import { environment } from "../../../../environments/environment";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";

@Injectable({
	providedIn: "root",
})
export class StartupService {
	api_url = environment.api_url;

	constructor(private http: HttpClient) {}

	saveStartupDetailsApi(post_data: any) {
		return this.http.post(this.api_url + `/providers/create`, post_data);
	}
	updateUserDetailsApi(post_data) {
		return this.http.post(this.api_url + `/users/update`, post_data);
	}
	uploadAvataar(post_data) {
		return this.http.post(this.api_url + `/providers/upload`, post_data);
	}

	uploadLogo(formData) {
		return this.http.post(this.api_url + `/providers/company_logo`, formData);
	}

	listLocations() {
		return this.http.post(this.api_url + `/locations/list`, {});
	}

	associateProviderLocation(post_data: any): Observable<any> {
		// console.log(post_data);
		return this.http.post<any>(this.api_url + `/locations_and_providers/create`, post_data);
	}

	async resendVerficationCode(postData) {
		return await this.http.post(this.api_url + `/users/resend_verification_code`, postData).toPromise();
	}
}
