import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";

import { ProviderComponent } from "./provider.component";
import { SolutionComponent } from "./solution/solution.component";
import { EditSolutionsComponent } from "./edit-solutions/edit-solutions.component";
import { PermissionGuard } from "../permission.guard";
import { SolutionCatalogingComponent } from "./solution-cataloging/solution-cataloging.component";
import { ApplicationCatalogingComponent } from "./application-cataloging/application-cataloging.component";
import { ProviderUserProfileComponent } from "./provider-user-profile/provider-user-profile.component";
import { ProviderPublicProfileComponent } from "./provider-public-profile/provider-public-profile.component";
import { ProviderUserEditComponent } from "./provider-user-edit/provider-user-edit.component";
import { AllSolutionsComponent } from "./all-solutions/all-solutions.component";
import { SolutionDirtyCheckGuard } from "./solution-dirty-check.guard";
import { EditsolutionDirtyCheckGuard } from "./edit-solutions/editsolution-dirty-check.guard";
import { UnsolicitedProposalComponent } from "./unsolicited-proposal/unsolicited-proposal.component";

const routes: Routes = [
	{
		path: "",
		component: ProviderComponent,
	},
	{
		path: "unsolicited-proposal",
		component: UnsolicitedProposalComponent

	},
	// {
	//   path: '',
	//   redirectTo: '/discover/providers',
	//   pathMatch: 'full'
	// },
	{
		path: ":slug",
		component: ProviderPublicProfileComponent,
		canActivate: [PermissionGuard],
		data: {
			permission: ["seeker"],
		},
	},

	{
		path: "user/:slug",
		component: ProviderUserProfileComponent,
		canActivate: [PermissionGuard],
		data: {
			permission: ["provider"],
		},
	},
	{
		path: "user/:slug/edit",
		component: ProviderUserEditComponent,
		pathMatch: "full",
		canActivate: [PermissionGuard],
		data: {
			permission: ["provider"],
		},
	},
	{
		path: "user/:slug/solution/:id/edit",
		component: EditSolutionsComponent,
		canActivate: [PermissionGuard],
		canDeactivate: [EditsolutionDirtyCheckGuard],
		data: {
			permission: ["provider"],
		},
	},
	{
		path: "user/:slug/solution/:id",
		component: SolutionComponent,
		canActivate: [PermissionGuard],
		data: {
			permission: ["provider"],
		},
	},
	{
		path: "user/:slug/solutions/create",
		component: SolutionCatalogingComponent,
		canActivate: [PermissionGuard],
		canDeactivate: [SolutionDirtyCheckGuard],
		data: {
			permission: ["provider"],
		},
	},
	{
		path: "user/:slug/application/create",
		component: ApplicationCatalogingComponent,
		canActivate: [PermissionGuard],
		data: {
			permission: ["provider"],
		},
	},
	{
		path: "user/:slug/all-solutions",
		component: AllSolutionsComponent,
		canActivate: [PermissionGuard],
		data: {
			permission: ["provider"],
		},
	},
	{
		path: "user/:slug/dashboard",
		loadChildren: () => import("./dashboard/dashboard.module").then((m) => m.DashboardModule),
		canActivate: [PermissionGuard],
		data: {
			permission: ["provider"],
		},
	},

];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class ProviderRoutingModule {}
