import { Component, Input, OnInit } from '@angular/core';
import { environment } from '../../../../environments/environment';

@Component({
  selector: 'enterprise-company-view',
  templateUrl: './enterprise-company-view.component.html',
  styleUrls: ['./enterprise-company-view.component.css']
})
export class EnterpriseCompanyViewComponent implements OnInit {
  @Input() profileData: any;
  serverUrl:string = environment.server_url;
  technical_expertises:any;
  founders:any;
  constructor() { }

  ngOnInit() {
    this.technical_expertises = this.profileData.enterprise_expertises.split(',');
    this.founders = this.profileData.enterprise_founders.split(',');
  }

}
