import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { CreateChallengeService } from 'src/app/challenge/create-challenge/create-challenge.service';
import { ToastrService } from "ngx-toastr";
import { unsolicitedProposalService } from './unsolicited-proposal.service';
import { promise } from 'protractor';
import { Select } from '@ngxs/store';
import { LoggedInUserState, LoggedInUserStateModel } from 'src/app/state/loggedinuser.state';
import { Observable } from 'rxjs';
import { LoggedInUser } from 'src/app/state/model/loggedinuser.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-unsolicited-proposal',
  templateUrl: './unsolicited-proposal.component.html',
  styleUrls: ['./unsolicited-proposal.component.css']
})
export class UnsolicitedProposalComponent implements OnInit {

  @Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
  loggedInUser: LoggedInUser;


  unsolicitedProposal: FormGroup;
  isSubmitted: boolean = false;
  challengeSectors: any;
  smartCities: any;
  isLoading:boolean = false;
	fileNameArr: string[];
	fileArr: any[] = [];
  uns_prop_files: any = [];



  constructor(private formBuilder: FormBuilder, private _challenge: CreateChallengeService, private toastr: ToastrService, private _ups: unsolicitedProposalService,private router:Router) {
    this.loggedInUser$.subscribe((resp) => {
      this.loggedInUser = resp.LoggedInUser;
    });
  }

  ngOnInit() {
    this.getChallengeSectors();
    this.getSmartCities();
    this.unsolicitedProposal = this.formBuilder.group({
      name: ['', [Validators.required]],
      challenge_sector: ['', Validators.required],
      city: ['', Validators.required]
    })
  }

  get formControlStep() {
    return this.unsolicitedProposal.controls;
  }

  async getChallengeSectors() {
    try {
      this.challengeSectors = await this._challenge.getChallengeSectors();
    } catch (err) {
      console.log(err);
      this.toastr.error("Something went wrong please try again", "Error");
    }
  }

  async getSmartCities() {
    try {
      let res: any = await this._ups.getSmartCities();
      this.smartCities = res.data;
    } catch (err) {
      this.toastr.error("Something went wrong please try again", "Error");
    }
  }


  extractFileName(path: string) {
		let filename = path.replace(/^.*[\\\/]/, "");
		return filename;
	}



  async onFileChange(event) {

    for (var i = 0; i < event.target.files.length; i++) {
      this.uns_prop_files.push(event.target.files[i]);
    }
    this.fileNameArr = this.uns_prop_files.map((d) => this.extractFileName(d.name));
}

removeFile(index){
  this.uns_prop_files.splice(index, 1);
  this.fileNameArr = this.uns_prop_files.map((d) => this.extractFileName(d.name));
}

async savePropsal() {
  if (this.unsolicitedProposal.valid && this.uns_prop_files.length) {
    this.isLoading = true;
    var post_data = {
      data: {
        name: this.unsolicitedProposal.value.name,
        frg_challenge_sector_id: this.unsolicitedProposal.value.challenge_sector,
        uns_proposal : true,
        chall_status:"active",
        frg_smart_city_id : this.unsolicitedProposal.value.city,
        un_status:"pending",
        frg_provider_id: this.loggedInUser.userTypeId
      }
    }
    let resp: any = await this._ups.createChallengeSmartcityAsscoiation(post_data);
    if(resp.status == true){
      this.uploadImages(resp.unp_data.id)
      this.toastr.success('', 'Unsolicited Proposal Created Successfully');
      this.router.navigate(["/provider/user/" + this.loggedInUser.slug + "/dashboard/all-applications"]);
    }
    else{
      this.toastr.error(resp.msg, "Error");
    }

  }
  else{
    this.toastr.error('', 'Please fill all the fields')
  }
}

async uploadImages(id){
  if (this.uns_prop_files.length > 0) {
    let formData = new FormData();
    formData.append("slug", id);
    this.uns_prop_files.forEach((file) => {
      formData.append("uns_files", file);
    });
    await this._ups.uploadImages(formData);
  }
}

}
