import { Component, Input, OnInit, OnChanges } from '@angular/core';
import { ProfileInfoService } from "../../profile-info.service"
import { ActivatedRoute, NavigationExtras, Router } from '@angular/router';
import { environment } from '../../../../environments/environment';
import * as _ from 'lodash';
import { ToastrService } from 'ngx-toastr'

@Component({
  selector: 'profile-top-card',
  templateUrl: './profile-top-card.component.html',
  styleUrls: ['./profile-top-card.component.css']
})
export class ProfileTopCardComponent implements OnInit, OnChanges {

  userSlug: string;
  serverUrl: string = environment.server_url;
  @Input() profileData: any;
  @Input() loading: boolean = true;
  @Input() viewType: string = 'public'
  constructor(
    private route: ActivatedRoute,
    private provider: ProfileInfoService,
    private toastr: ToastrService,
    private router: Router
  ) {
  }

  ngOnInit() { }

  ngOnChanges() { 
    // console.log("ng on changes",this.profileData)
  }

  async fetchUserProfile(slug:string) {
    try {
      this.loading = true;
      this.profileData = await this.provider.getProfile(slug);
      this.loading = false;
    } catch (err) {
      console.log(err);
      this.toastr.error('', 'Something went wrong please try again');
    }
  }
  editSectors(){
    var query_params:NavigationExtras ={
      queryParams:{
        mode:"update"
      }
    }
    this.router.navigate(['/discover/select-sector'],query_params)
  }
  
}
