import { Injectable } from "@angular/core";
import { environment } from "../../../environments/environment";
import { HttpClient } from "@angular/common/http";

@Injectable({
	providedIn: "root",
})
export class ApplicationService {
	api_url = environment.api_url;
	constructor(private http: HttpClient) {}

	async createNewApplication(postData) {
		return await this.http.post(this.api_url + "/application/create_send_push_city_admin", postData).toPromise();
	}

	async uploadApplicationImages(formData) {
		return await this.http.post(this.api_url + "/application/uploads", formData).toPromise();
	}

	async getAnchorsBasedOnChallenges(formData){
		return await this.http.post(this.api_url + "/anchors/select_mul", formData).toPromise();
	}

	async updateUnspropStatus(post_data:any){
		return await this.http.post(this.api_url + "/unsolicited_proposal/update", post_data).toPromise();
	}
}
