import { Injectable } from "@angular/core";
import { environment } from "src/environments/environment";
import { HttpClient } from "@angular/common/http";

@Injectable({
	providedIn: "root",
})
export class EditSolutionsServiceService {
	api_url = environment.api_url;
	constructor(private http: HttpClient) {}

	async getSolution(solutionId: number) {
		return await this.http
			.post(this.api_url + "/solutions/select", {
				slug: { id: solutionId },
			})
			.toPromise();
	}

	async updateSolution(data) {
		return await this.http.post(this.api_url + "/solutions/update", data).toPromise();
	}

	async getChallengeSectors() {
		return await this.http
			.post(this.api_url + "/challenge_sectors/select_mul", {
				pg: {
					offset: 0,
					limit: 200,
				},
				filter: {
					status:'active'
				}
			})
			.toPromise();
	}

	async getChallenges(sectorId) {
		return await this.http
			.post(this.api_url + "/challenges/select_mul", {
				filter: {
					frg_challenge_sector_id: sectorId,
					status:'active'
				},
				pg: {
					offset: 0,
					limit: 400,
				},
			})
			.toPromise();
	}

	async addChallenges(data) {
		return await this.http.post(this.api_url + "/solutions_challenges/create", data).toPromise();
	}

	async addChallengeSectors(data) {
		return await this.http.post(this.api_url + "/solutions_challenge_sectors/create", data).toPromise();
	}

	async getSolutionsChallengesAssoc(solutionId: number) {
		return await this.http
			.post(this.api_url + "/solutions_challenges/select_mul", {
				filter: {
					frg_solution_id: solutionId,
				},
			})
			.toPromise();
	}

	async getSolutionsChallengeSectorsAssoc(solutionId: number) {
		return await this.http
			.post(this.api_url + "/solutions_challenge_sectors/select_mul", {
				filter: {
					frg_solution_id: solutionId,
				},
			})
			.toPromise();
	}

	async deleteSolutionChallengesAssoc(ids: number[]) {
		return await this.http
			.post(this.api_url + "/solutions_challenges/delete_mul", {
				slug: { ids: ids },
			})
			.toPromise();
	}

	async deleteSolutionChallengeSectorsAssoc(ids: number[]) {
		return await this.http
			.post(this.api_url + "/solutions_challenge_sectors/delete_mul", {
				slug: { ids: ids },
			})
			.toPromise();
	}

	async removeFromProductImages(data) {
		return await this.http.post(this.api_url + "/solutions/remove_from_images", data).toPromise();
	}

	async addtoProductImages(data) {
		return await this.http.post(this.api_url + "/solutions/add_to_images", data).toPromise();
	}

	async uploadSolutionImages(formData) {
		return await this.http.post(this.api_url + "/solutions/uploads", formData).toPromise();
	}

	// async removeFromGallery(data){
	//   return await this.http.post(this.api_url+"/smart_cities/remove_from_gallery",data).toPromise();
	// }
	// async getFilteredChallengeSectorSmartCities(postData){
	//   return await this.http.post(this.api_url+`/smart_cities_and_challenge_sectors/select_mul`,postData).toPromise();
	// }
	// async deleteCityChallenge(data){
	//   return await this.http.post(this.api_url+`/challenges_and_smart_cities/delete_mul`, data).toPromise();
	// }
	// async getSmartCityChallenge(data){
	//   return await this.http.post(this.api_url+`/challenges_and_smart_cities/select_mul`, data).toPromise();
	// }
	// // async getCityChallenges(data) {
	// //   return await this.http.post(this.api_url + "/challenges/select_mul", data).toPromise();
	// // }

	// async deleteExisitingSmartCityChallengeSectors(postData){
	//   return await this.http.post(this.api_url+`/smart_cities_and_challenge_sectors/delete_mul`,postData).toPromise();
	// }

	async getSectorIdsFollowedByUser(innovatorId: number) {
		let resp: any = await this.http
			.post(this.api_url + "/challenge_sectors_and_providers/select_mul", {
				filter: {
					frg_provider_id: innovatorId,
				},
			})
			.toPromise();
		let sectorIds: number[] = resp.data.map((e) => e.frg_challenge_sector_id);
		return sectorIds;
	}
}
